* {
  margin: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

html {
  width: 800px;
  height: 500px;
  overflow: hidden;
}

.window {
  width: 800px;
  height: 500px;
  border: 10px groove #4050de;
  background-color: #cfd8dc;
}
img {
  border-radius: 50%;
  width: 75px;
  height: 75px;
  margin: 10px;
}

nav {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  width: 100%;
  box-sizing: border-box;
  font-size: 20px;
  font-weight: 600;
}

nav a {
  text-decoration: none;
  padding: 20px 15px 20px 15px;
  color: white;
  border-radius: 5%;
}
nav a:hover {
  background-color: white;
  color: black;
}

.middle {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

.content {
  margin-top: 20px;
  display: grid;
  grid-template-columns: 2.5fr 1fr 2.5fr;
}

.primary,
.secondary {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-content: center;
}

input {
  text-align: center;
  border-radius: 5px;
  border: 2px solid #aaa;
  height: 60px;
  width: 225px;
  margin-top: 10px;
  padding: 8px;
  transition: 0.3s;
  margin-left: auto;
  margin-right: auto;
  font-size: 25px;
}
#primaryValue:focus {
  border-color: dodgerblue;
  box-shadow: 0 0 8px 0 dodgerblue;
}
select {
  width: 225px;
  margin: auto;
  height: 80px;
  font-size: 50px;
  text-align: center;
  font-weight: 700;
}
select option {
  font-size: 30px;
}

.switchButton {
  font-size: 75px;
}
.content p {
  font-size: 22px;
  margin-top: 40px;
}

.chart {
  width: 80%;
  height: 60%;
  margin-left: auto;
  margin-right: auto;
}

p {
  font-size: 20px;
  font-weight: 600;
  padding: 10px;
}

.exchange p {
  font-size: 17px;
  margin-top: 10px;
}

.chartSelect {
  display: flex;
  justify-content: space-between;
}

h3 {
  font-size: 22px;
  padding: 10px;
  text-decoration: underline;
}
.top {
  padding: 10px;
}
.bottom {
  margin-top: 10px;
  padding: 10px;
}
